@import ../../../styles/helpers

.container
    max-width: 1440px
    +w
        padding: 0 40px
    +t
        padding: 0

.wrapper
    padding: 80px
    border-radius: 24px
    background: $n7
    +w
        padding: 64px 40px
    +t
        border-radius: 0
    +m
        padding: 64px 32px
    +dark
        background: $n1
        box-shadow: inset 0 0 0 2px $n2

.title
    margin-bottom: 12px

.info
    margin-bottom: 64px
    +d
        margin-bottom: 48px

.sorting
    display: flex
    align-items: center
    margin-bottom: 48px
    +t
        justify-content: space-between
    +m
        display: block

.nav
    display: flex
    justify-content: center
    margin-right: auto
    +t
        display: none

.link
    display: flex
    padding: 6px 12px
    border-radius: 14px
    background: none
    +button-2
    color: $n4
    transition: all .2s
    svg
        margin-right: 8px
        fill: $n4
        transition: fill .2s
    &:hover
        color: $n3
        svg
            fill: $n3
        +dark
            color: $n6
            svg
                fill: $n6
    &.active
        background: $n3
        color: $n8
        svg
            fill: $n8
        +dark
            background: $n6
            color: $n2
            svg
                fill: $n2
    &:not(:last-child)
        margin-right: 16px
        
.box
    flex-shrink: 0
    width: 256px
    +m
        width: 100%
        &:not(:last-child)
            margin-bottom: 16px

.list
    display: flex
    flex-wrap: wrap
    margin: -32px -16px 0
    +x
        margin: -32px -10px 0
    +d
        margin: -32px -16px 0
    +m
        display: block
        margin: 0

.card
    flex: 0 0 calc(33.333% - 32px)
    width: calc(25% - 32px)
    margin: 32px 16px 0
    +x
        flex: 0 0 calc(33.333% - 20px)
        width: calc(25% - 20px)
        margin: 32px 10px 0
    +d
        flex: 0 0 calc(33.333% - 32px)
        width: calc(33.333% - 32px)
        margin: 32px 16px 0
    +t
        flex: 0 0 calc(50% - 32px)
        width: calc(50% - 32px)
    +m
        display: block
        width: 100%
        margin: 32px 0 0
    &:nth-child(n+7)
        +d
            display: none

.btns
    margin-top: 64px
    text-align: center
    +t
        margin-top: 48px
    