// common styles
body
    min-width: 375px
    background: $n8
    +poppins
    font-size: 14px
    line-height: (24/14)
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
    color: $n2
    +dark-body
        background: $n1
        color: $n8

a
    text-decoration: none

svg,
img
    vertical-align: middle

.desktop
    &-hide
        +d
            display: none !important
    &-show
        display: none !important
        +d
            display: block !important
    &-text-right
        +d
            text-align: right

.tablet
    &-hide
        +t
            display: none !important
    &-show
        display: none !important
        +t
            display: block !important
            
.mobile
    &-hide
        +m
            display: none !important
    &-show
        display: none !important
        +m
            display: block !important

div[data-testid="plane-loading"]
    position: fixed
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    z-index: 999
    width: 100%
    height: 100%
    align-items: center
    justify-content: center
    background: rgb(255 255 255 / 89%)
    svg
        path
            stroke: #000000!important
        path:last-child
            fill: #3B71FE!important
            stroke: none!important
.otpfieldwrap
    justify-content: center
.otpfieldwrap
    input
        height: 55px!important
        width: 55px!important
        border-radius: 100%!important
        border: 1px solid $n10
        background: $white
        text-align: center
        +poppins
        font-size: 18px
        font-weight: 400
        color: $n2
        transition: all .2s
        &:focus
            border:2px solid $p5
            background: $white
        +dark
            background: $n2
            border-color: $n2
            color: $n8
            &:focus
                background: none
                border-color: $n3
div[data-testid="otp-resend-root"]
    justify-content: center!important
    padding: 10px 0
    flex-direction: row-reverse!important

div[data-testid="otp-resend-root"]
    button
        margin-right: 8px
        color: $n4
