@import ../../styles/helpers

.revieopinner
    h2
        color: $n2
        font-size: 24px
        font-weight: 500
        margin-bottom: 5px

.ratesec
    display: flex
    justify-content: space-between
    margin-bottom: 15px
    .ratetitle
        strong
            font-size: 14px
            font-weight: 400
            color: $n2
    .satrs
        img
            height: 20px

.commentsec
    .inputgroup
        padding:10px
        border: 2px solid $n6
        border-radius: 15px
        display: flex 
        align-items: center
.commentsec
    .inputgroup
        input
            width: 70%
            font-size: 16px
            color: $n2
        .emojis
            width: 7%
            svg
                fill: $n4

        .sendbtn
            width: 22%
            padding: 15px 15px
            background: $p1
            color: $white
            font-size: 14px
            border-radius: 30px
            svg
                fill: $white
                margin-left: 5px


.filterboxr
    display: flex
    justify-content: space-between
    margin: 15px 0
    h3
        font-size: 24px
        font-weight: 500
        color: $n2
        
.reviewsbox
    .reviewsboxlist
        max-height: 300px
        overflow: auto
        
.reviewsboxitem
    display: flex
    justify-content: space-between
    padding-bottom: 15px
    margin-bottom: 15px
    border-bottom: 1px solid $n6
    .authorreview
        width: 15%
        padding-right: 15px
        img
            width: 100%
    .contentreivew
        width: 84%
        .titlerev
            display: flex
            justify-content: space-between
            margin-bottom: 10px
            h4
                font-size: 14px
                font-weight: 500
                color: $n1
            img
                height: 16px


.descrev
    p
        font-size: 14px
        font-weight: 400
        color: $n3
.revmeta
    margin: 15px 0 0 
    a
        font-size: 12px
        font-weight: 600
        color: $n2
        display: inline-block
        margin-right: 15px
        span
            font-weight: 400
            color: $n4



