@import ../../styles/helpers

.reviewcols
    width: 50%

.reviewcols
    .reviewbox
        padding: 25px
        background: $n8
        border: 1px solid $n6
        box-sizing: border-box
        box-shadow: 0 0 14px $n6
        border-radius: 24px

.reviewcols
    .reviewbox
        .reviewheader
            display: flex
            align-items: center
            justify-content: space-between
            margin-bottom: 15px

.reviewauthor
    margin-right: 18px

.reviwauthdetail
    h3
        font-size: 18px
        color: $n2
        font-weight: 500
    p
        font-size: 15px
        color: $n9
        line-height: 22px
    
.expandsc
    padding: 10px 0
    a
        color: $n4
        font-size: 16px
        font-weight: normal
        text-decoration: underline

.revrwatesec
    padding-bottom: 15px
    ul
        li
            font-size: 15px
            margin-bottom: 12px
            display: flex
            justify-content: space-between
            .detailrevstar,
            .subtitlereviw
                color: $n4
            .titlereviw
                color: $n2
            
.aboutcomapne
    padding-top:15px
    border-top: 1px solid $n9
    p
        font-size: 15px
        color: $n4
        margin-bottom: 15px
    h3
        margin-bottom: 10px
        font-weight: 500

.revstar
    img
        height: 24px