@import ../../../styles/helpers

.inner
    position: relative

.head
    margin-bottom: 80px
    padding-right: 120px
    +d
        margin-bottom: 64px
    +m
        margin-bottom: 40px
        padding: 0

.headSmall
    margin-bottom: 48px
    +m
        margin-bottom: 40px

.info
    margin-top: 12px

.wrapper
    margin: 0 -16px
    +m
        margin: 0 -8px

.featuredsection
    padding: 30px 0

.sliderwrap
    display: flex
    flex-wrap: wrap
    column-gap: 20px

.sliderwrap
    .card
        width: 31.3%
        margin-bottom: 15px

.boxwrap
    width: 48%
    box-sizing: border-box

.boxwrap
    .card
        width: 100
        flex-grow: 0
        flex-shrink: 0
        flex-basis: calc(100% - 0px)
        width:100%
        margin:0


.boxwrapinner
    padding: 25px
    background: #FCFCFD
    border: 1px solid #E6E8EC
    box-sizing: border-box
    box-shadow: 0 0 14px #e6e8ec
    border-radius: 24px
    margin-bottom: 30px

.boxheaders
    display: flex
    align-items: center
    .boxmainimg
        width:36%
        padding-right: 15px
        img
            width: 100%
            height: 140px
            object-fit: cover
            border-radius: 10px
.boxwrap
    .author
        display: flex
        align-items: center
        .text
            margin-right: 4px
            +caption-2
            color: $n4
.boxwrap
    .avatar
        flex-shrink: 0
        width: 24px
        height: 24px
        margin-right: 8px
        img
            width: 100%
            height: 100%
            border-radius: 50%
            object-fit: cover
.boxwrap
    .line
        display: flex
        flex-wrap: wrap
        align-items: center
.boxwrap
    .rating
        display: flex
        align-items: center 
        margin-bottom: 10px
        +m
            flex-wrap: wrap
        svg
            margin-right: 8px
            fill: $s3

.boxwrap
    .number
        font-weight: 500

.boxwrap
    .reviews
        margin-left: 8px
        color: $n4
    
.boxwrap
    .number,.man
        color: $n2

.boxtitlesection
    width: 64%

.boxtitlesection
    .title
        font-size: 24px
        color: $n2
        display: block
        margin-bottom: 10px
        font-weight: 500
        line-height: 24px

.boxtitlesection
    .rastingsection
        padding-top: 15px
        margin-top: 20px
        border-top: 1px solid $n6

.boxmainsection
    .boxdatesection
        display: flex
        align-items: center
        justify-content: space-between
        column-gap: 8px
        padding: 20px 0

.boxmainsection
    .boxdatesection
        .boxdate
            small
                font-size: 12px
                display: block
                color: $n4
                line-height: 14px
            p
                font-size: 16px
                color: $n2
                font-weight: 500
                line-height: 24px
                margin-top: 4px

.boxbookingdetail
    h3
        font-size: 24px
        color: $n2
        font-weight: 500
        margin: 10px 0 20px
    .bookinglisting
        padding: 20px
        background: #EEF3FF
        margin: 20px 0 0px
        border-radius: 15px
        li
            display: flex
            justify-content: space-between
            align-items: center
            margin: 5px 0 12px
            .titlebook
                font-size: 14px
                font-weight: 400
                color: $n4
                img
                    height: auto
                    width: 17px
                    margin-right: 10px
            .bookdesc
                strong
                    color: $n2
                    font-weight: 500
                    font-size: 14px
.bookinglisting
    li:first-child
        .titlebook
            img
                width: auto
                height: 17px
                margin-right: 16px

.warnningtext
    font-size: 12px
    color: $n4
    margin: 15px 0 0px
    img
         height: 14px
         margin-right:4px

.textlink
        display: inline-block        
        color: $n4
        font-size: 14px
        text-decoration: underline
