@import ../../../styles/helpers

.title
    margin-bottom: 48px
    text-align: center
    +m
        margin-bottom: 40px

.nav
    display: flex
    flex-wrap: wrap
    justify-content: center
    margin: -12px -6px 40px
    +m
        display: none

.btn
    margin: 12px 6px 0
    padding: 6px 12px
    border-radius: 14px
    background: none
    +button-2
    color: $n4
    transition: all .2s
    -webkit-tap-highlight-color: rgba(0,0,0,0)
    &:hover
        color: $n3
        +dark
            color: $n8
    &.active
        background: $n3
        color: $n8
        +dark
            background: $n8
            color: $n2

.dropdown
    display: none
    text-align: left
    +m
        display: block
        margin-bottom: 24px

.list
    max-width: 546px
    margin: 0 auto

.faqsection
    padding: 40px 0
    width: 100%
    display: flex
    flex-wrap: wrap

.faqsection
    .leftfaq
        width: 40%
        padding-right: 20px
        
.faqsection
    .rightfaq
        width: 60%
        padding-left: 20px

.faqsection
    .rightfaq
        a
            background: #fff
            border: 1px solid $n6
            color: $n2
            display: inline-block
            padding: 9px 20px
            border-radius: 30px
            margin-top: 30px

.faqsection
    .rightfaq
        .list
            width: 100%
            max-width: 100%
.faqsection
    .leftfaq
        .list
            width: 100%
            max-width: 100%
.faqsection
    .leftfaq
        .list
            div
                div
                    padding:8px 0px    
.faqsection
    .rightfaq
        .list
            div
                div
                    padding:8px 0px