@import ../../styles/helpers


.popwrap
    padding: 20px

.popwrapinner
    .btnwrappop
        display: flex
        justify-content: space-between
        flex-wrap: wrap
        margin-bottom: 15px

.popwrapinner
    .btnwrappop
        .piolttext
            width: 60%

.popwrapinner
    .btnwrappop
        .piolttext
            p
                font-size: 18px
                font-weight: 500  
            span
                display: block
                font-size: 11px
                color: $n4      
.popwrapinner
    .btnwrappop
        .btpilot
                width: 40%
                text-align: right 
          

.lispiots
    li
        .lispiotsinner
            display: flex
            justify-content: space-between
            padding: 15px
            border-radius: 15px
            margin-bottom: 20px
        .imgpiots
            width: 10%
        .datapiots
            width: 60%
        .pricepiots
            width: 30%

.lispiots
    li
        .lispiotsinner
            &:hover
                background: $n11

.lispiots
    li
        .lispiotsinner
            .imgpiots
                img
                    border-radius: 100%


.lispiots
    li
        .lispiotsinner
            .datapiots
                h3
                    font-size: 22px
                p
                    font-size: 12px
                    color: $n4
                a
                    font-size: 12px
                    color: $n4
                    cursor: pointer
                    text-decoration: underline
                ul.metapiolt
                    margin-top: 10px
                    li
                        font-size: 12px
                        color: $n4
                        margin-bottom: 2px
                        margin-bottom: 8px
                ul.metapiolt
                    li
                        span
                            margin-right: 4px
                            display: inline-block
                            width: 25px
                        img
                            width: auto
                            height: 20px
                ul.metapiolt
                    li
                        strong
                            color: $n1

.pricepiots
    h2
        font-size: 22px
        font-weight: 500
        text-align: right

    span
        color: $n4
        font-size: 14px

.btnfootermodel
    display: flex
    column-gap: 15px

.btnfootermodel
    a
        display: block
        padding: 10px 15px
        width: 50%
        text-align: center
        font-size: 15px
        border-radius: 8px

    a.btnborderd
        background: $white
        border: 1px solid $n6
        color: $n2
        &:hover
            background: $p1
            color: $white
            border-color: $p1

    a.btnfill
        background: $p1
        border: 1px solid $p1
        color: $white
        &:hover
            background: $n1
            color: $white
            border-color: $n1

    