@import ../../styles/helpers
.appwrap
    padding: 15px 0 8px

.appwrapinner
    display: flex
    justify-content: space-between
    align-items: center

.appwrapinner
    .applogo
           img
            height: 40px
            width: auto
            margin-left: 10px

.textapp
    p
    color: $n2
    font-size: 14px

.footer
    border-top: 1px solid $n6
    +dark
        border-color: $n2


.row
    display: flex
    padding: 80px 0 48px
    +m
        display: block
        padding-top: 64px

.col
    &:first-child       
        width: 10%
        +m
            flex-direction: row
            width: 100%
            margin-bottom: 48px
    &:nth-child(2)
        width: 15%
        padding: 0 16px
        +m
            margin-bottom: 48px
            padding: 0
    &:nth-child(3)
        width: 15%
        padding: 0 16px
        +m
            margin-bottom: 48px
            padding: 0
    &:nth-child(4)
        width: 15%
        padding: 0 16px
        +m
            margin-bottom: 48px
            padding: 0
    &:nth-child(5)
        width: 15%
        padding: 0 16px
        +m
            margin-bottom: 48px
            padding: 0
    &:nth-child(6)
        width: 20%
        padding: 0 16px
        +m
            margin-bottom: 48px
            padding: 0

.col
    h3
     color: $p1
     font-size: 14px
     line-height: 16px
     font-weight: 700
     margin-bottom: 15px

.logo
    margin: -8px 0 16px
    +m
        margin-right: auto
    img
        width: 70px

.box
    display: flex
    align-items: center
    +button-2
    color: $n4
    & > span
        margin-right: 8px
    svg
        margin-right: 8px
        fill: $n4

.menu
    display: block
    flex-wrap: wrap

.socailmenu
    ul
        li
            margin-bottom: 9px

.socailmenu
    ul
        li
            img
                height: 20px
                margin-right:6px   

.socailmenu
    ul
        li
            a
                color: $n4
                +dm-sans
                font-weight: 700

.socailmenu
    ul
        li
            a
               &:hover
                        color: $n2

.link
    display: block
    width: calc(100% - 0px)
    margin: 0
    +dm-sans
    font-weight: 700
    +button-2
    color: $n4
    transition: color .2s
    +t
        flex: 0 0 calc(100% - 0px)
        width: calc(100% - 0px)
    &:hover
        color: $n2
        +dark
            color: $n6
    &:nth-child(3)
        +t
            order: 5
.link
    line-height: 25px
.info
    margin-bottom: 16px
    +hairline-2

.bottom
    padding: 24px 0
    display: flex
    justify-content: space-between
    align-items: center
    border-top: 1px solid $n7
    +dark
        border-color: $n2

.bottom
    p
    color: $n2
    font-size: 12px
     